: justify;\n}\n#sectionHome .home-img {\n  padding-top: 2%;\n  text-align: center;\n}\n#sectionHome .home-img img {\n  height: 100%;\n  width: 95vw;\n}\n#sectionService.grid {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: space-around;\n}\n\n#sectionService.grid .services {\n  display: flex;\n  flex-direction: column;\n  width: 48%;\n  text-align: center;\n}\n\n#sectionService.grid .services .service-img {\n  width: 100%;\n  height: 25vw;\n}\n\n#sectionService.grid .services .info {\n  display: none;\n}\n#sectionFooter {\n  background: var(--darker);\n  color: var(--white);\n  text-align: center;\n  padding: 2%;\n  font: bold 13px/18px verdana;\n  min-height: 200px;\n}\n.model .layout {\n  position: fixed;\n  top: 0px;\n  left: 0px;\n  width: 100%;\n  height: 100%;\n  background: var(--darker);\n  opacity: 0.5;\n}\n.model .popup {\n  width: 90%;\n  top: 15%;\n  left: 5%;\n  padding: 2% 5%;\n  position: fixed;\n  color: var(--darker);\n  background: var(--white);\n}\n.model .popup form {\n  display: grid;\n  grid-template-columns: 2fr 5fr;\n  grid-gap: 10px;\n  padding: 10px;\n  justify-content: space-around;\n}\n.model .popup .close {\n  position: absolute;\n  cursor: pointer;\n  padding: 0px 6px;\n  right: 2%;\n  top: 2%;\n}\n.model .popup form button {\n  margin-top: 20px;\n  height: 35px;\n  width: 150px;\n}\n.model .serviceImg {\n  height: 100px;\n  width: 160px;\n  object-fit: cover;\n}\n@media (min-width: 600px) {\n  .services {\n    flex-direction: row;\n    min-height: 21vw;\n  }\n  .services .service-img {\n    width: 22vw;\n    height: 18vw;\n  }\n  #sectionService.grid .services .service-img {\n    height: 20vw;\n  }\n  .model .popup {\n    width: 60%;\n    left: 20%;\n  }\n}\n\n@media (min-width: 850px) {\n  .parent {\n    flex-direction: column;\n  }\n  header {\n    width: 20%;\n    height: 100%;\n    /*min-width: 225px;*/\n  }\n  .header {\n    padding: 12% 0px;\n  }\n  .logo img {\n    width: 100%;\n  }\n  .menu {\n    display: none;\n  }\n  #navbar {\n    display: block !important;\n    height: 78%;\n  }\n  #navbar .navChild {\n    padding: 10px;\n    text-align: left;\n  }\n  main {\n    width: 80%;\n    margin-left: 20%;\n    margin-top: 0px;\n  }\n  #sectionHome .home-img img {\n    height: 80%;\n    width: 78vw;\n  }\n  .services {\n    flex-direction: row;\n    min-height: 15vw;\n  }\n  .services .service-img {\n    width: 18vw;\n    height: 13vw;\n  }\n  #sectionService.grid .services {\n    width: 26%;\n  }\n  #sectionService.grid .services .service-img {\n    height: 18vw;\n  }\n}\n\n@media (min-width: 1300px) {\n  #sectionService {\n    display: flex;\n    flex-direction: row;\n    flex-wrap: wrap;\n    justify-content: space-between;\n  }\n  #sectionService .services {\n    /*flex-direction: column;*/\n    width: 49%;\n    min-height: 12vw;\n  }\n  .services .service-img {\n    width: 16vw;\n    height: 11vw;\n    object-fit: cover;\n  }\n  #sectionService.grid .services .service-img {\n    height: 16vw;\n  }\n}\n\n/*****Hamburger CSS *****/\n.bar {\n  height: 5px;\n  width: 45px;\n  display: block;\n  margin: 8px auto;\n  background-color: #222;\n}\n\n.menu.close .bar:nth-of-type(1) {\n  -moz-transform: translateY(15px) rotate(45deg);\n  -ms-transform: translateY(15px) rotate(45deg);\n  -webkit-transform: translateY(15px) rotate(45deg);\n  transform: translateY(15px) rotate(45deg);\n}\n.menu.close .bar:nth-of-type(2) {\n  -moz-transform: translateX(100px);\n  -ms-transform: translateX(100px);\n  -webkit-transform: translateX(100px);\n  transform: translateX(100px);\n  -moz-transition: 0s;\n  -o-transition: 0s;\n  -webkit-transition: 0s;\n  transition: 0s;\n}\n.menu.close .bar:nth-of-type(3) {\n  -moz-transform: translateY(-12px) rotate(-48deg);\n  -ms-transform: translateY(-12px) rotate(-48deg);\n  -webkit-transform: translateY(-12px) rotate(-48deg);\n  transform: translateY(-12px) rotate(-48deg);\n}\n.about-head {\n  text-align: center;\n  color: var(--dark);\n  font-size: 32px;\n  font-family: MONOSPACE;\n}\n.about-detail {\n  padding: 15px;\n  font-size: 1.2rem;\n  color: #685f5f;\n  text-align: justify;\n  line-height: 1.5;\n  box-shadow: 1px 2px 3px var(--darker);\n  background: #fff;\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  border-radius: 5px;\n  width: 60vw;\n  margin: auto;\n}\n.about-detail img {\n  object-fit: cover;\n  width: 55vw;\n  opacity: 0.6;\n  box-shadow: 3px 3px 5px var(--darker);\n  height: 50vh;\n}\n.contact-detail img {\n  object-fit: cover;\n  width: 25vw;\n  margin: auto;\n}\n.error,\n.info-popup {\n  position: fixed;\n  padding: 11px;\n  top: 5%;\n  right: 3%;\n  background: #cb3700;\n  border-radius: 8px;\n  z-index: 99999;\n  color: #fff;\n}\n.info-popup {\n  background: #039e40;\n}\n#sectionService.grid img {\n  object-fit: cover;\n}\n.contact-detail {\n  padding: 60px;\n  font-size: 24px;\n  color: #685f5f;\n  text-align: justify;\n  line-height: 1.5;\n  box-shadow: 1px 2px 3px var(--darker);\n  background: #fff;\n  margin: 25px;\n  display: flex;\n  flex-direction: column;\n  gap: 20px;\n  border-radius: 5px;\n  margin: auto;\n  text-align: center;\n}\n.search-field {\n  height: 35px;\n  width: 20vw;\n  border-radius: 20px;\n  margin: auto;\n  padding: 15px;\n}\n.footer {\n  background: var(--darker);\n  color: var(--white);\n  text-align: center;\n  font-size: 12px;\n  padding: 2%;\n}\n"],"sourceRoot":""}]);
